.nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  padding: 20px;
  height: 30px;

  justify-content: space-between;
  z-index: 9999;

  transition-timing-function: ease-in-out;
  transition: all 0.8s;
}
.nav_black {
  background-color: #111111;
}
.nav_logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}

.nav_avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
